import React, {useState, useEffect} from 'react';
import Papa from 'papaparse';
import {Pie} from 'react-chartjs-2';
import {
    Chart as ChartJS,
    ArcElement,
    Tooltip,
    Legend,
} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels'; // Import the plugin
import { Context } from 'chartjs-plugin-datalabels';
import crypto from 'crypto-js';
import './App.css';

// Register the necessary components for Chart.js
ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);

interface LICOData {
    CommunitySize: string;
    HouseholdType: string;
    HouseholdSize: string;
    BeforeTax: string;
    AfterTax: string;
    Green: string;
    Yellow: string;
    Red: string;
}

interface ExpenditureData {
    Food: number;
    Shelter: number;
    "Water, fuel, and electricity": number;
    Transportation: number;
    "Other expenses": number;
}

interface ExpenditureByType {
    HouseholdType: string;
    Food: string;
    Shelter: string;
    "Water, fuel, and electricity": string;
    Transportation: string;
    "Other expenses": string;
}

interface ExpenditureByTypeIncome {
    HouseholdType: string;
    Begin: number;
    End: number;
    Food: string;
    Shelter: string;
    "Water, fuel, and electricity": string;
    Transportation: string;
    "Other expenses": string;
}


const MainPage: React.FC = () => {
    const [licoData, setLICOData] = useState<LICOData[]>([]);
    const [communitySizes, setCommunitySizes] = useState<string[]>([]);
    const [householdTypes, setHouseholdTypes] = useState<string[]>([]);
    const [householdSizes, setHouseholdSizes] = useState<string[]>([]);
    console.log(householdSizes)
    const [filteredHouseholdSizes, setFilteredHouseholdSizes] = useState<string[]>([]);
    const [selectedCommunitysize, setSelectedCommunitySize] = useState<string>('');
    const [selectedHouseholdType, setSelectedHouseholdType] = useState<string>('');
    const [selectedHouseholdSize, setSelectedHouseholdSize] = useState<string>('');

    const [beforeTaxIncome, setBeforeTaxIncome] = useState<number | null>(null);
    const [afterTaxIncome, setAfterTaxIncome] = useState<number | null>(null);
    const [displayedIncome, setDisplayedIncome] = useState<number | null>(null);
    
    const [firstValue, setIncomeFirstValue] = useState<number | null>(null);
    const [secondValue, setSecondValue] = useState<number | null>(null);

    const [isBeforeTax, setIsBeforeTax] = useState(false); // State to control the visibility of the paragraph
    
    const [inputAfterTaxIncome, setInputAfterTaxIncome] = useState<string>(''); // State to manage the value in the input textbox
    const [inputBeforeTaxIncome, setInputBeforeTaxIncome] = useState<string>(''); // State to manage the value in the input textbox
    const [policyProfileText, setPolicyProfileText] = useState<string>('');

    const [expenditureData, setExpenditureData] = useState<ExpenditureData | null>(null);
    const [expenditureByType, setExpenditureByType] = useState<ExpenditureByType[]>([]); // Initialize with an empty array
    const [expenditureByTypeIncome, setExpenditureByTypeIncome] = useState<ExpenditureByTypeIncome[]>([]); // Initialize with an empty array

    //const [showPopup, setShowPopup] = useState(true); // State to control the visibility of the popup
    //const [isClosing, setIsClosing] = useState(false); // State to handle the closing animation

    //const [isDefExpanded, setIsDefExpanded] = useState(true); // State to control the visibility of the paragraph
    const [isDef2Expanded, setIsDef2Expanded] = useState(true); // State to control the visibility of the paragraph
    const [isDef3Expanded, setIsDef3Expanded] = useState(true); // State to control the visibility of the paragraph
    
    const [incomeHeaderText, setIncomeHeaderText] = useState('Income Scale (After Tax):');

    useEffect(() => {
        // Load and parse the CSV file
        Papa.parse<LICOData>(require('./LICO.csv'), {
            download: true,
            header: true,
            complete: (result: Papa.ParseResult<LICOData>) => {
                const data = result.data;

                setLICOData(data);
                // Get unique values for dropdowns
                const uniqueCommunitySizes: string[] = Array.from(new Set(data.map((item : LICOData) => item.CommunitySize)));
                const uniqueHouseholdTypes: string[] = Array.from(new Set(data.map((item: LICOData) => item.HouseholdType)));
                const uniqueHouseholdSizes: string[]= Array.from(new Set(data.map((item: LICOData) => item.HouseholdSize)));

                setCommunitySizes(uniqueCommunitySizes);
                setHouseholdTypes(uniqueHouseholdTypes);
                setHouseholdSizes(uniqueHouseholdSizes);
            },
        });

        // Load and parse the Expenditure_Pattern_All_Households.csv file
        Papa.parse<ExpenditureData>(require('./Expenditure_Pattern_All_Households.csv'), {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (result: Papa.ParseResult<ExpenditureData>) => {
                const data = result.data;
                //console.log(expenditureData)

                if (data.length > 0) {
                    setExpenditureData({
                        Food: data[0]['Food'],
                        Shelter: data[0]['Shelter'],
                        "Water, fuel, and electricity": data[0]['Water, fuel, and electricity'],
                        Transportation: data[0]['Transportation'],
                        "Other expenses": data[0]['Other expenses'],
                    });

                }
            },
        });

        // Load and parse the Expenditure_Pattern_All_Households_by_Household_Type.csv file
        Papa.parse<ExpenditureByType>(require('./Expenditure_Pattern_All_Households_by_Household_Type.csv'), {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (result: Papa.ParseResult<ExpenditureByType>) => {
                setExpenditureByType(result.data as ExpenditureByType[]);
            },
        });

        // Load and parse the Expenditure_Pattern_All_Households_by_Household_Type_and_Income.csv file
        Papa.parse<ExpenditureByTypeIncome>(require('./Expenditure_Pattern_All_Households_by_Household_Type_and_Income.csv'), {
            download: true,
            header: true,
            dynamicTyping: true,
            complete: (result: Papa.ParseResult<ExpenditureByTypeIncome>) => {
                setExpenditureByTypeIncome(result.data as ExpenditureByTypeIncome[]);
            },
        });

    }, []);


    const filteredData = expenditureByType.find((item) => item.HouseholdType === selectedHouseholdType);

    let filteredDataIncome = null;
    
    if (isBeforeTax) {
        filteredDataIncome = expenditureByTypeIncome.find((item) => item.HouseholdType === selectedHouseholdType && beforeTaxIncome !== null && item.End > beforeTaxIncome);
    }
    else {
        filteredDataIncome = expenditureByTypeIncome.find((item) => item.HouseholdType === selectedHouseholdType && afterTaxIncome !== null && item.End > afterTaxIncome);
    }


    const data = {
        labels: ['Food', 'Shelter', 'Water, fuel, and electricity', 'Transportation', 'Other expenses'],
        datasets: [
            {
                data: expenditureData ? [
                    expenditureData.Food,
                    expenditureData.Shelter,
                    expenditureData["Water, fuel, and electricity"],
                    expenditureData.Transportation,
                    expenditureData["Other expenses"]
                ] : [],
                backgroundColor: ['blue', 'green', 'brown', 'gray', 'orange'],
                datalabels: {
                    color: '#ffffff', // Set label color to white
                    formatter: (value: number, context: any) => {
                        const total = context.chart.data.datasets[0].data.reduce((acc: number, curr: number) => acc + curr, 0);
                        
                        if (total && typeof total === 'number') {
                          const percentage = ((value / total) * 100).toFixed(1) + '%';
                          return percentage;
                        }
                        else {
                          return 0;
                        }
                    },
                },
            },
        ],
    };


    const dataByType = {
        labels: ['Food', 'Shelter', 'Water, fuel, and electricity', 'Transportation', 'Other expenses'],
        datasets: [
            {
                data: filteredData ? [
                    parseFloat(filteredData.Food),
                    parseFloat(filteredData.Shelter),
                    parseFloat(filteredData["Water, fuel, and electricity"]),
                    parseFloat(filteredData.Transportation),
                    parseFloat(filteredData["Other expenses"])
                ] : [],
                backgroundColor: ['blue', 'green', 'brown', 'gray', 'orange'],
                datalabels: {
                    color: '#ffffff', // Set label color to white
                    formatter: (value: number, context: any) => {
                        const total = context.chart.data.datasets[0].data.reduce((acc: number, curr: number) => acc + curr, 0);
                        
                        if (total && typeof total === 'number') {
                          const percentage = ((value / total) * 100).toFixed(1) + '%';
                          return percentage;
                        }
                        else {
                          return 0;
                        }
                    },
                },
            },
        ],
    };

    const dataByTypeIncome = {
        labels: ['Food', 'Shelter', 'Water, fuel, and electricity', 'Transportation', 'Other expenses'],
        datasets: [
            {
                data: filteredDataIncome ? [
                    parseFloat(filteredDataIncome.Food),
                    parseFloat(filteredDataIncome.Shelter),
                    parseFloat(filteredDataIncome["Water, fuel, and electricity"]),
                    parseFloat(filteredDataIncome.Transportation),
                    parseFloat(filteredDataIncome["Other expenses"])
                ] : [],
                backgroundColor: ['blue', 'green', 'brown', 'gray', 'orange'],
                datalabels: {
                    color: '#ffffff', // Set label color to white
                    formatter: (value: number, context: any) => {
                        const total = context.chart.data.datasets[0].data.reduce((acc: number, curr: number) => acc + curr, 0);
                        
                        if (total && typeof total === 'number') {
                          const percentage = ((value / total) * 100).toFixed(1) + '%';
                          return percentage;
                        }
                        else {
                          return 0;
                        }
                    },
                },
            },
        ],
    };


    const optionsByType = {
        plugins: {
            legend: {
                display: !!selectedHouseholdType, // Conditionally display legend
            },
        },
    };

    useEffect(() => {
        // Filter household sizes based on selected community size and household type
        if (selectedHouseholdType) {
            const filteredSizes = licoData
                .filter((item) => item.HouseholdType === selectedHouseholdType)
                .map((item) => item.HouseholdSize);

            const uniqueFilteredSizes = Array.from(new Set(filteredSizes));
            setFilteredHouseholdSizes(uniqueFilteredSizes);

            // Automatically select the first available household size
            if (uniqueFilteredSizes.length > 0) {
                setSelectedHouseholdSize(uniqueFilteredSizes[0]);
            } else {
                setSelectedHouseholdSize(''); // Clear the selection if no sizes are available
            }

        } else {
            setFilteredHouseholdSizes([]);
            setSelectedHouseholdSize(''); // Clear the selection if nothing is filtered
        }
    }, [selectedCommunitysize, selectedHouseholdType, licoData]);

    useEffect(() => {
        // Update first and second value when all selections are made
        if (selectedCommunitysize && selectedHouseholdType && selectedHouseholdSize) {
            const matchingRecord = licoData.find(
                (item) =>
                    item.CommunitySize === selectedCommunitysize &&
                    item.HouseholdType === selectedHouseholdType &&
                    item.HouseholdSize === selectedHouseholdSize
            );
            if (matchingRecord) {
                
                if (!isBeforeTax) {
                    const income = Number(matchingRecord.AfterTax);
                    const secondValue = income * 1.25
                    setIncomeFirstValue(income);
                    setSecondValue(secondValue);
    
                    let profileText = '';
    
                    // Determine the user's status based on after tax income
                    if (afterTaxIncome !== null) {
                        if (afterTaxIncome >= 0 && afterTaxIncome < income) {
                            profileText = matchingRecord.Red || 'Not available';
                        } else if (afterTaxIncome >= income && afterTaxIncome < secondValue) {
                            profileText = matchingRecord.Yellow || 'Not available';
                        } else if (afterTaxIncome >= secondValue) {
                            profileText = matchingRecord.Green || 'Not available';
                        }
                        setPolicyProfileText(profileText);
                    }
                }
                else {

                    const income = Number(matchingRecord.BeforeTax);
                    const secondValue = income * 1.25
                    setIncomeFirstValue(income);
                    setSecondValue(secondValue);
    
                    let profileText = '';
    
                    // Determine the user's status based on after tax income
                    if (beforeTaxIncome !== null) {
                        if (beforeTaxIncome >= 0 && beforeTaxIncome < income) {
                            profileText = matchingRecord.Red || 'Not available';
                        } else if (beforeTaxIncome >= income && beforeTaxIncome < secondValue) {
                            profileText = matchingRecord.Yellow || 'Not available';
                        } else if (beforeTaxIncome >= secondValue) {
                            profileText = matchingRecord.Green || 'Not available';
                        }
                        setPolicyProfileText(profileText);
                    }
                }

            } else {
                setIncomeFirstValue(null);
                setSecondValue(null);
                setPolicyProfileText('Not available');
            }
        } else {
            setPolicyProfileText('Not available');
        }
    }, [selectedCommunitysize, selectedHouseholdType, selectedHouseholdSize, afterTaxIncome, beforeTaxIncome, licoData, isBeforeTax]);

    // Calculate the position of the indicator on the scale
    const getIndicatorPosition = (value: number | null): string => {
        if (value !== null) {
            const scaleMax = 100000;
            const position = (value / scaleMax) * 100; // Convert to percentage
            return `${position}%`;
        }
        return '0%';
    };

    // Function to calculate the gradient for the income scale
    const getGradient = (): string => {
        if (firstValue !== null && secondValue !== null) {
            const scaleMax = 100000;

            // Calculate percentage positions for first and second value
            const firstPosition = (firstValue / scaleMax) * 100;
            const secondValuePosition = (secondValue / scaleMax) * 100;

            return `linear-gradient(to right, red 0%, yellow ${firstPosition}%, yellow ${firstPosition}%, yellow ${secondValuePosition}%, yellow ${secondValuePosition}%, green 100%)`;

        }
        return 'linear-gradient(to right, red, green)'; // Default gradient if no value is set
    };

    // Function to handle clicks on the income scale line
    const handleScaleClick = (e: React.MouseEvent<HTMLDivElement, MouseEvent>): void => {
        const scaleElement = e.currentTarget;
        const rect = scaleElement.getBoundingClientRect();
        const clickX = e.clientX - rect.left; // Get the X position of the click relative to the scale
        const scaleWidth = rect.width;

        const scaleMax = 100000;
        const newIncomeValue = Math.round((clickX / scaleWidth) * scaleMax); // Calculate the income value based on the click position

        setDisplayedIncome(newIncomeValue);

        if (!isBeforeTax) {
            setAfterTaxIncome(newIncomeValue); // Update the after tax income state    
            setInputAfterTaxIncome(newIncomeValue.toString()); // Update the input textbox
            setInputBeforeTaxIncome('')
        }
        else {
            setBeforeTaxIncome(newIncomeValue); // Update the after tax income state
            setInputBeforeTaxIncome(newIncomeValue.toString()); // Update the input textbox
            setInputAfterTaxIncome('')
        }

    };

    const handleBeforeTaxIncomeChange = (e: React.ChangeEvent<HTMLInputElement>) : void => {
        const value = e.target.value;
        setInputBeforeTaxIncome(value);

        // Try to convert the input value to a number
        const numericValue = Number(value);
        if (!isNaN(numericValue)) {
            setBeforeTaxIncome(numericValue);
            setInputAfterTaxIncome('');
            setDisplayedIncome(numericValue)
            setIncomeHeaderText("Income Scale (Before Tax):")
            setIsBeforeTax(true)
        }
    }

    const handleAfterTaxIncomeChange = (e: React.ChangeEvent<HTMLInputElement>) : void => {
        const value = e.target.value;
        setInputAfterTaxIncome(value); // Update the inputIncome state with the new value

        // Try to convert the input value to a number
        const numericValue = Number(value);
        if (!isNaN(numericValue)) {
            setAfterTaxIncome(numericValue); // Update the after tax income state if it's a valid number
            setInputBeforeTaxIncome('');
            setDisplayedIncome(numericValue)
            setIncomeHeaderText("Income Scale (After Tax):")
            setIsBeforeTax(false)
        }

    };

    // const closePopup = () : void => {
    //     setIsClosing(true);
    //     setTimeout(() => setShowPopup(false), 300); // Wait for the zoom-out animation to complete
    // };

    //const toggleExpand = () : void => {
    //    setIsDefExpanded(!isDefExpanded); // Toggle the state between true and false
    //};

    const toggleExpand2 = () : void => {
        setIsDef2Expanded(!isDef2Expanded); // Toggle the state between true and false
    };

    const toggleExpand3 = () : void => {
        setIsDef3Expanded(!isDef3Expanded); // Toggle the state between true and false
    };

    return (
        <div className="container">

            {/* {showPopup && (
                <div className={`popup ${isClosing ? 'zoom-out' : ''}`}>
                    <div className="popup">
                        <div className="popup-content">
                            <p>This tool allows you to select a household by community size, household type, and household
                                size, and find where it sits on the income scale chart and its spending pattern. You can
                                set an annual income value by clicking on the income scale.</p>
                            <button onClick={closePopup}>Close</button>
                        </div>
                    </div>
                </div>
            )} */}

            <header>
                <h1>Interactive Affordability Assessment Tool</h1>
                <p>This tool allows you to select a household by community size, household type, household size, and before/after tax income, and 
                    find where it sits on the income scale chart and its spending pattern. <br /> <br /> This tool uses the income thresholds in Low-Income Cut-Offs (LICOs).  
                    </p>
            </header>

            <section className="define-household">
                <h2>Define a household:</h2>
                <div className="filters">
                    <div className="filter-item">
                        <label htmlFor="communitySize">Community Size</label>
                        <select
                            id="communitySize"
                            value={selectedCommunitysize}
                            onChange={(e) => setSelectedCommunitySize(e.target.value)}
                        >
                            <option value="">Select Community Size</option>
                            {communitySizes.map((communitySize) => (
                                <option key={communitySize} value={communitySize}>
                                    {communitySize}
                                </option>
                            ))}
                        </select>
                    </div>

                    <div className="filter-item">
                        <label htmlFor="household-type">Household Type</label>
                        <select
                            id="household-type"
                            value={selectedHouseholdType}
                            onChange={(e) => setSelectedHouseholdType(e.target.value)}
                        >
                            <option value="">Select Household Type</option>
                            {householdTypes.map((type) => (
                                <option key={type} value={type}>
                                    {type}
                                </option>
                            ))}

                        </select>
                    </div>

                    <div className="filter-item">
                        <label htmlFor="household-size">Household Size</label>
                        <select
                            id="household-size"
                            value={selectedHouseholdSize}
                            onChange={(e) => setSelectedHouseholdSize(e.target.value)}
                            disabled={!filteredHouseholdSizes.length} // Disable if no options available
                        >
                            <option value="">Select Household Size</option>
                            {filteredHouseholdSizes.map((size) => (
                                <option key={size} value={size}>
                                    {size}
                                </option>
                            ))}
                        </select>
                    </div>
                </div>
                <br/>
                <div className="filters">

                    <div className="filter-item">
                        <label htmlFor="income">Household Annual Before-Tax Income (2023)</label>
                        <input
                            type="number"
                            id="income"
                            placeholder="Enter income"
                            value={inputBeforeTaxIncome}
                            step="200" // Set the increment/decrement step to 100
                            onChange={(e) => handleBeforeTaxIncomeChange(e)}  // Handle manual user input
                        />
                    </div>

                    <div className="filter-item">
                        <label htmlFor="income">Household Annual After-Tax Income (2023)</label>
                        <input
                            type="number"
                            id="income"
                            placeholder="Enter income"
                            value={inputAfterTaxIncome}
                            step="200" // Set the increment/decrement step to 100
                            onChange={(e) => handleAfterTaxIncomeChange(e)}  // Handle manual user input
                        />
                    </div>
                </div>
                <br/>

            </section>

            <section className="income-scale">
                <h2>{incomeHeaderText}</h2><br/>
                <div className="scale">
                    <div
                        className="line"
                        style={{
                            background: getGradient(),
                        }}
                        onClick={handleScaleClick} // Add the click event handler here
                    >
                        <span className="low">$0</span>
                        <span className="high">$100,000</span>
                    </div>
                    {firstValue !== null && (
                        <div
                            className="indicator"
                            style={{
                                left: getIndicatorPosition(firstValue),
                            }}
                        >
                            <div className="indicator-label">Poverty</div>
                            ${Math.round(firstValue).toLocaleString()}
                        </div>
                    )}
                    {secondValue !== null && (
                        <div
                            className="indicator2"
                            style={{
                                left: getIndicatorPosition(secondValue),
                            }}
                        >
                            <div className="indicator-label">At-Risk Groups</div>
                            ${Math.round(secondValue).toLocaleString()}
                        </div>
                    )}
                    {displayedIncome !== null && (
                        <div
                            className="indicator income-indicator"
                            style={{
                                left: getIndicatorPosition(displayedIncome),
                            }}
                        >
                            <div className="income-label">Annual Income</div>
                            ${Math.round(displayedIncome).toLocaleString()}
                        </div>
                    )}
                </div>
                <br/><br/><br/>

                <section className="definition-section">
                    <button onClick={toggleExpand2} className="toggle-button">
                        {isDef2Expanded ? '-' : '+'}
                    </button>
                    {isDef2Expanded && (
                        <p><b>Poverty</b>: Statistics Canadas Low-income Cut-Offs (LICOs) are the most widely available measures of low-income in Canada. 
                        The LICOs are income thresholds below which a family will likely devote a larger share of its income on the necessities of food,
                         shelter, and clothing than the average family. The approach is essentially to estimate an income threshold at which families are 
                         expected to spend 20 percentage points more than the average family on food, shelter, and clothing. An individual is considered 
                         to be in low-income if his/her total family income is below the LICO, and a family is in low-income if its total income is below the LICO.<br/>
                            <b>At-Risk Groups</b>: A proxy for at-risk groups is defined as 125% of LICO.
                        </p>
                    )}
                </section>

            </section>

            <section className="spending-pattern">
                <h2>Spending Pattern (2023):</h2>
                <div className="charts">
                    <div className="chart">
                        <h3>Average Households</h3>
                        <br/><br/>
                        {expenditureData && <Pie data={data}/>}
                    </div>
                    <div className="chart">
                        <h3>{selectedHouseholdType ? `${selectedHouseholdType} ` : 'Household Type'}</h3>
                        <br/><br/>
                        {filteredData && <Pie data={dataByType} options={optionsByType}/>}
                    </div>
                    <div className="chart">
                        <h3>{selectedHouseholdType ? `${selectedHouseholdType} ` : 'Household Type'} by
                            Income</h3>
                        <p>{filteredDataIncome ? '$' + filteredDataIncome.Begin.toLocaleString() : ''} {filteredDataIncome ? ' - ' : ''} {filteredDataIncome ? '$' + filteredDataIncome.End.toLocaleString() : ''}</p>
                        {filteredDataIncome ? filteredDataIncome.Food ? filteredDataIncome &&
                            <Pie data={dataByTypeIncome}
                                 options={optionsByType}/> : 'Data not available ' : ''}
                    </div>
                </div>
                <br/>

                <section className="definition-section">
                    <button onClick={toggleExpand3} className="toggle-button">
                        {isDef3Expanded ? '-' : '+'}
                    </button>
                    {isDef3Expanded && (
                        <p>Treasury Board and Finance conducted an analysis on the structure of household
                        expenditures for different household types across various income brackets in Alberta for 2023. This
                        analysis utilized microdata from Statistics Canadas 2019 Survey of Household Spending, which
                        gathered data from over 17,000 Canadian households across all provinces and territories.</p>
                    )}
                </section>

            </section>

            <section className="policy-profile">
                <h2>Policy Profile:</h2>
                <p>{policyProfileText}</p>
            </section>

            <footer>
                <p>This is a test site only.</p>
            </footer>

        </div>
    );
};

const LandingPage = ({ onAccessGranted }: { onAccessGranted: () => void }) => {
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    // Store the hashed password
    const correctPasswordHash = 'a9ea89a687a13ffdd118306824f4060724d57fd3d60541506dca72f8774c6e25';

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        const hashedInput = crypto.SHA256(password).toString();

        if (hashedInput === correctPasswordHash) {
            console.log('Access granted.')
            onAccessGranted();
        } else {
            setError('Incorrect password.');
        }
    };

    return (
        <div className="landing-page" style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            height: '100vh',
            fontSize: '1.5rem'
        }}>

            <h2>Enter Password</h2>
            <form onSubmit={handleSubmit} style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="Enter password"
                    style={{ marginBottom: '10px', fontSize: '1.2rem' }}
                /><br/>
                <button type="submit">Submit</button>
            </form>
            {error && <p className="error" style={{ color: 'red', marginTop: '10px', fontSize: '1.2rem' }}>{error}</p>}
        </div>
    );
};


const App: React.FC = () => {
    const [hasAccess, setHasAccess] = useState(false);

    React.useEffect(() => {
        document.title = 'Assessment Tool';
    }, []);

    return (
        <div className="app">
            {hasAccess ? (
                <MainPage />
            ) : (
                <LandingPage onAccessGranted={() => setHasAccess(true)} />
            )}
        </div>
    );
};

export default App;
